// Context.js
import React from "react"
import { parse } from "query-string"
// import { IntlProvider } from "react-intl";
// import "../i18n/config/reactIntl";
// import translations from "../i18n/translations";

const defaultContextValue = {
  data: {
    // set your initial data shape here
    galleryState: "all",
  },
  set: () => {},
}

const { Provider, Consumer } = React.createContext(defaultContextValue)

class ContextProviderComponent extends React.Component {
  constructor(props) {
    super(props)
    this.setData = this.setData.bind(this)
    this.state = {
      ...defaultContextValue,
      set: this.setData,
    }
    // this.state.data = {
    //   galleryState: this.props.location.search
    //     ? this.props.location.search
    //     : "all",
    // }
  }

  setData(newData) {
    this.setState(state => ({
      data: {
        ...state.data,
        ...newData,
      },
    }))
  }

  render() {
    // this.setState({ galleryState: "all11" })
    console.log("location.search parse", parse(this.props.location.search).f)
    // this.state.data = {
    //   galleryState: parse(this.props.location.search).f
    //     ? parse(this.props.location.search).f
    //     : "all",
    // }
    return (
      <Provider value={this.state}>
        {console.log("Context props: ", this.props.location)}
        {this.props.children}
      </Provider>
    )
  }
}

export { Consumer as default, ContextProviderComponent }
