import React from "react"
import Hover from "../images/hover.svg"
import "../styles/galleryItem.css"
class GalleryItem extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      hoverState: false,
    }
  }
  handleBoxToggle = () => this.setState({ hoverState: !this.state.hoverState })

  render() {
    return (
      <div
        role="button"
        tabIndex="0"
        className={this.props.className}
        style={this.props.style}
        onMouseEnter={this.handleBoxToggle}
        onMouseLeave={this.handleBoxToggle}
      >
        {this.props.children}
        {this.state.hoverState && (
          <div
            className="absolute top-0 bottom-0 left-0 right-0 h-full w-full flex flex-col pixel"
            style={{ display: this.state.hoverState ? "block" : "hidden" }}
          >
            {/* <div className="absolute top-0 bottom-auto text-gray-100 px-2 pt-2 w-full text-2xl font-nav text-center selection-text z-10">
              STATUS
            </div> */}
            <h1 className="absolute top-auto bottom-0 text-gray-100 px-2 pb-2 w-full text-xl font-nav text-center selection-text z-10">
              {this.props.alt}
            </h1>

            <Hover className={"selection"} />
          </div>
        )}
      </div>
    )
  }
}
export default GalleryItem
