import React, { useState, useEffect } from "react"
import { graphql } from "gatsby"
// import ReactDOM from "react-dom"
import Img from "gatsby-image"
import { parse } from "query-string"
import LocalizedLink from "../components/LocalizedLink"
import SEO from "../components/seo"
import ContextConsumer from "../components/Context"
import AutoResponsive from "autoresponsive-react"
import GalleryItem from "../components/galleryItem"

import { useTranslation } from "react-i18next"

export const IndexPage = ({ data, ...props }) => {
  const { t } = useTranslation()

  // const [containerWidth, setContainerWidth] = useState(0)
  const [divRef, setDivRef] = useState({})

  const [width, setWidth] = useState(0)

  useEffect(() => {
    const resizeListener = () => {
      // change width from the state object
      if (divRef)
        setWidth(
          window.innerWidth ||
            document.documentElement.clientWidth ||
            document.body.clientWidth
        )
    }

    window.addEventListener("resize", resizeListener)

    // console.log("DEV REF:", divRef)
    if (divRef) {
      // console.log("DEV REF:", divRef.clientWidth)

      setWidth(divRef.clientWidth)
    }
    // clean up function
    return () => {
      // remove resize listener
      window.removeEventListener("resize", resizeListener)
    }
  }, [divRef])

  const getAutoResponsiveProps = () => {
    return {
      itemMargin: 0,
      containerWidth: width,
      itemClassName: "item",
      gridWidth: 1,
      transitionDuration: ".5",
      transitionTimingFunction: "easeInOut",
    }
  }
  const filteredProjects = val => {
    // console.log("galleryState [", val, "]")
    const projects = data.allProjectsJson.nodes
    if (val !== "3d" && val !== "art") {
      // console.log("GalleryFiltering: ", this.props.data)
      return projects
    } else {
      return projects.filter(element => element.tags.some(tag => tag === val))
    }
  }
  const GalleryItems = val => {
    if (width === undefined) return
    // const files = data.allProjectsJson.nodes;
    const files = filteredProjects(val)

    return files.map(element => {
      const projectPath = element.i18n.find(l => l.locale === "en").path
      const projectAlt = element.i18n.find(
        l => l.locale === props.pageContext.locale
      ).alt
      // console.log("element cover: ", element.node.cover)
      let coverImage
      if (element.size === "2") {
        // console.log("Double: ", projectAlt)
        coverImage = data.doubleImage.nodes.find(i => i.name === element.cover)
          .childImageSharp.fluid
      } else {
        // console.log("Single: ", projectAlt)
        coverImage = data.singleImage.nodes.find(i => i.name === element.cover)
          .childImageSharp.fluid
      }
      /**
       * sm (640px)	max-width: 640px;
       * md (768px)	max-width: 768px;
       * lg (1024px)	max-width: 1024px;
       * xl (1280px)	max-width: 1280px;
       */
      let divider = 2
      if (width > 640) divider = 4
      if (width > 1280) divider = 8

      let size = (width / divider) * element.size
      // element.node.size === "1"
      // ? width / divider / 2
      // : width / divider

      // let sizepx = size.toString() + "px"
      // console.log("Size: ", sizepx, " container: ", width)
      return (
        <div
          className="item pixel"
          style={{ width: size + "px", height: size + "px" }}
          key={element.project_id}
        >
          <LocalizedLink to={"/" + projectPath}>
            <GalleryItem alt={projectAlt} desc="description">
              <Img
                fluid={coverImage}
                alt={projectAlt}
                // imgStyle={{ width: sizepx, height: sizepx }}
              />
            </GalleryItem>
          </LocalizedLink>
        </div>
      )
      // console.log(images.find(i => (i.node.name = element.node.cover)));
    })
  }
  return (
    <div className="text-white" ref={element => setDivRef(element)}>
      {/* {console.log("DEV REF", divRef.clientWidth)} */}
      {/* {width} F: {parse(props.location.search).f} */}
      {/* {console.log("Index props", data)} */}
      <SEO title={t("index.title")} />
      <ContextConsumer>
        {({ data }) => {
          return (
            <AutoResponsive {...getAutoResponsiveProps()}>
              {GalleryItems(
                parse(props.location.search).f
                  ? parse(props.location.search).f
                  : ""
              )}
            </AutoResponsive>
          )
        }}
      </ContextConsumer>
    </div>
  )
}

export const query = graphql`
  query {
    allProjectsJson {
      nodes {
        cover
        project_id
        size
        tags
        i18n {
          alt
          desc
          locale
          path
        }
      }
    }
    doubleImage: allFile(
      filter: { sourceInstanceName: { eq: "projectsImages" } }
    ) {
      nodes {
        sourceInstanceName
        relativeDirectory
        name
        childImageSharp {
          fluid(
            maxWidth: 640
            maxHeight: 640
            quality: 100
            srcSetBreakpoints: [640]
          ) {
            ...GatsbyImageSharpFluid_withWebp_noBase64
          }
        }
      }
    }
    singleImage: allFile(
      filter: { sourceInstanceName: { eq: "projectsImages" } }
    ) {
      nodes {
        sourceInstanceName
        relativeDirectory
        name
        childImageSharp {
          fluid(
            maxWidth: 320
            maxHeight: 320
            quality: 100
            srcSetBreakpoints: [320]
          ) {
            ...GatsbyImageSharpFluid_withWebp_noBase64
          }
        }
      }
    }
  }
`
export default IndexPage
